import './common.css';
import './App.css';
import { useEffect, useState } from 'react'
import { useNavigate, Routes, Route } from 'react-router-dom'
import { useSelector } from 'react-redux'
import Campaign from './routes/Campaign'
import User from './routes/User'
import axios from 'axios';

function App() {

  // inform-popup      .
  //    
  const [popupShowYn, setPopupShowYn] = useState(false)
  
  //    user[0]  
  const user = useSelector((state) => {return state.user})

  /*
      
    -  : , , 
    -  : 
       0 ,  1,  2 
  */

 //    state
  const [userType, setUserType] = useState('0')

  const changeUserType = e => {
    setUserType(parseInt(e.target.value))
  }

  useEffect(() => {
   // console.log('  ')
  }, [userType])
  
  const navigate = useNavigate()

  return (
    <div className="App">
      <ul className="menubar-wrap">
        <li>Wisebirds</li>
        <li onClick={()=>{ navigate('/')}}></li>
        { userType == '0' ? <li onClick={()=> { navigate('/user')}}></li> : null }
        <li className="user-email"
            onClick={()=>{ setPopupShowYn(!popupShowYn) }}>{user[0].email}
            { popupShowYn === true ? <Popup user={user[0]}/> : null }
        </li>
        <li>
          <select defalutValue="0" onChange={changeUserType}>
            <option value="0"></option>
            <option value="1"></option>
            <option value="2"></option>
          </select>
        </li>
      </ul>
      <Routes>
          <Route path ="/" element={<Campaign userType={userType} />}></Route>
					<Route path="/user" element={<User userType={userType} />}></Route>
			</Routes>
      
    </div>
  );
}

function Popup({user}) {
  return (
    <div className="inform-popup">
      <div>{user.name}</div>
      <div>{user.email}</div>
      <div>{user.company.name}</div>
    </div>
  )
}
export default App;
